import React from 'react';
import { BarChart, Bar, XAxis, YAxis, Legend } from 'recharts';
import Button from '@material-ui/core/Button'

const DataChart = props => {

    const { table } = props


    const data = [];
    const timeData = {};

    for (let i = 0; i < 24; i++) timeData[i] = 0


    for (let i = 1; i < table.length; i++) {
        let hours = table[i][2].split(':')
        let minutes = table[i][4].split(':')


        timeData[+hours[0]] += +minutes[1]
    }

    for (let i = 0; i < 24; i++) {
        data.push({ name: i, 'minutes of the hour': timeData[i] })
    }

    return (
        <div>
            <BarChart width={1300} height={400} data={data} margin={{ top: 50, right: 30, left: 20, bottom: 5 }}>
                <XAxis dataKey="name" stroke="#8884d8" />
                <YAxis />
                <Legend />
                <Bar type="monotone" dataKey="minutes of the hour" barSize={20} fill="#8884d8"
                    label='label' />
            </BarChart>
            <Button variant="contained" type="submit" color="primary" onClick={generator}>GENERATE</Button>
        </div>

    )
}

const generator = () => {
    const amount = Math.floor(Math.random()*5+10)
    const newTable = []
    
    for(let i = 0; i < amount; i++) {

        newTable.push([])
        const duration = Math.floor(Math.random()*80+10)
        const hours = Math.floor(Math.random()*23)
        const minutes = Math.floor(Math.random()*59)
        newTable[i][0] = i
        newTable[i][1] = 'Random name'
        newTable[i][2] = timeForm(hours, minutes, duration).timeStart
        newTable[i][3] = timeForm(hours, minutes, duration).timeEnd
        newTable[i][4] = timeForm(hours, minutes, duration).duration
    }
    console.log(newTable)
}

const timeForm = (hoursStart, minutesStart, duration) => {
    let hours = hoursStart
    let minutes = minutesStart + duration;
    if (minutes > 59) {
        minutes-= 60
        hours++
        if (hours > 23) hours = 0         
    }
    let durMinutes = duration
    let durHours = 0
    if (durMinutes > 60) {
        durMinutes-=60
        durHours++
    }
    return {
        timeStart: `${hoursStart < 10 ? '0'+hoursStart : hoursStart}:${minutesStart < 10 ? '0'+minutesStart : minutesStart}:00`,
        timeEnd: `${hours < 10 ? '0'+hours : hours}:${minutes < 10 ? '0'+minutes : minutes}:00`,
        duration: `${durHours < 10 ? '0'+durHours : durHours}:${durMinutes < 10 ? '0'+durMinutes : durMinutes}:00`
    }
}

export default DataChart